async uploadLargeAttachment(leadId, attachment) {
    const CHUNK_SIZE = 500 * 1024; // 500KB par chunk
    const totalChunks = Math.ceil(attachment.datas.length / CHUNK_SIZE);
    let attachmentId = null;

    log(`Dbut upload par chunks pour ${attachment.name}`, {
        totalSize: attachment.datas.length,
        chunks: totalChunks
    });

    for (let i = 0; i < totalChunks; i++) {
        const start = i * CHUNK_SIZE;
        const end = Math.min(start + CHUNK_SIZE, attachment.datas.length);
        const chunk = attachment.datas.slice(start, end);
        const isLastChunk = i === totalChunks - 1;

        try {
            const chunkResponse = await this.fetchWithTimeout(
                `${this.worker_url}/upload_attachment_chunk`,
                {
                    method: 'POST',
                    headers: this.buildHeaders(),
                    body: JSON.stringify({
                        odoo_url: this.host,
                        database: this.db,
                        username: this.username,
                        password: this.password,
                        attachment_data: {
                            name: attachment.name,
                            datas: chunk,
                            res_model: 'crm.lead',
                            res_id: leadId,
                            mimetype: attachment.mimetype,
                            chunk_index: i,
                            total_chunks: totalChunks,
                            is_last_chunk: isLastChunk,
                            attachment_id: attachmentId // null pour le premier chunk
                        }
                    })
                },
                15000 // 15 secondes par chunk
            );

            const result = await chunkResponse.json();
            
            if (!result.success) {
                throw new Error(result.error || 'chec upload chunk');
            }

            // Rcuprer l'ID de l'attachment aprs le premier chunk
            if (i === 0) {
                attachmentId = result.attachment_id;
            }

            updateProgress(attachment.name, 'progress', `Upload en cours... ${Math.round((i + 1) / totalChunks * 100)}%`);

        } catch (error) {
            throw new Error(`Erreur chunk ${i + 1}/${totalChunks}: ${error.message}`);
        }
    }

    return attachmentId;
}


    async testServerConnection() {
        log('Test de connexion au serveur', {
            url: this.worker_url,
            odoo_url: this.host
        });

        return this.retryRequest(async () => {
            try {
                const response = await this.fetchWithTimeout(
                    `${this.worker_url}/test`,
                    {
                        method: 'POST',
                        headers: this.buildHeaders(),
                        body: JSON.stringify({
                            odoo_url: this.host,
                            database: this.db,
                            username: this.username,
                            password: this.password
                        })
                    }
                );

                if (!response.ok) {
                    const errorData = await response.json().catch(() => ({}));
                    throw new Error(errorData.error || `Erreur HTTP ${response.status}`);
                }

                const data = await response.json();
                log('Rponse du test de connexion', data);

                // Vrification de l'authentification
                const loginSuccess = data.details?.login_response?.includes('<value><int>') ||
                                   data.details?.login_response?.includes('<value><boolean>1</boolean>');

                if (!loginSuccess) {
                    throw new Error('chec de l\'authentification. Vrifiez vos identifiants de connexion.');
                }

                const versionInfo = this.extractVersionInfo(data.details.version_response);
                
                return {
                    success: true,
                    message: `Connexion tablie avec succs (Odoo ${versionInfo.version})`,
                    details: {
                        version: versionInfo,
                        responses: data.details
                    }
                };

            } catch (error) {
                return this.handleError(error, 'Erreur lors du test de connexion');
            }
        });
    }

