// Modifier la fonction switchPage pour inclure la page de recherche
function switchPage(pageId) {
    const mainPage = document.getElementById('app-body');
    const settingsPage = document.getElementById('settings-page');
    const searchPage = document.getElementById('search-page');
    
    // Reset all pages
    mainPage.classList.remove('slide-left');
    settingsPage.classList.remove('active');
    searchPage.classList.remove('active');
    
    switch(pageId) {
        case 'settings-page':
            mainPage.classList.add('slide-left');
            settingsPage.classList.add('active');
            break;
        case 'search-page':
            mainPage.classList.add('slide-left');
            searchPage.classList.add('active');
            break;
        default:
            // Retour  la page principale
            break;
    }
}

document.getElementById('search-button').addEventListener('click', async () => {
    try {
        // Basculer vers la page de recherche
        switchPage('search-page');
        document.getElementById('back-button').classList.add('visible');
        
        const item = Office.context.mailbox.item;
        
        if (item) {
            // Traitement de l'expditeur (mme logique que loadEmailData)
            if (item.sender) {
                let senderEmail = item.sender.emailAddress;
                senderEmail = cleanEmail(senderEmail);
                document.getElementById('search-sender-email').value = senderEmail;
            }

            // Traitement du corps du message de manire asynchrone
            try {
                const bodyResult = await new Promise((resolve, reject) => {
                    item.body.getAsync(Office.CoercionType.Text, (result) => {
                        if (result.status === Office.AsyncResultStatus.Succeeded) {
                            resolve(result);
                        } else {
                            reject(new Error('chec du chargement du corps du message'));
                        }
                    });
                });

                const fullText = bodyResult.value;

                // Dtecter les numros de tlphone (mme logique que loadEmailData)
                const phones = extractPhoneNumbers(fullText);
                log('Numros de tlphone dtects pour la recherche:', phones);
                
                // Mettre  jour le select des numros de tlphone
                const phoneSelect = document.getElementById('search-phone-select');
                
                // Vider le select sauf l'option par dfaut
                while (phoneSelect.options.length > 1) {
                    phoneSelect.remove(1);
                }

                // Ajouter les numros trouvs
                phones.forEach((phoneInfo, index) => {
                    const option = document.createElement('option');
                    option.value = phoneInfo.formatted;
                    option.textContent = `Numro ${index + 1}: ${phoneInfo.formatted}`;
                    phoneSelect.appendChild(option);
                });

                // Si des numros ont t trouvs, remplir l'input avec le premier
                if (phones.length > 0) {
                    document.getElementById('search-phone-number').value = phones[0].formatted;
                }

            } catch (error) {
                log('Erreur lors du chargement du corps du message:', error);
                throw error;
            }
        }

    } catch (error) {
        console.error('Erreur lors du chargement des donnes:', error);
        showNotification('Erreur lors du chargement des donnes', 'error', {
            message: error.message
        });
    }
});

